@import "../../../../assets/styles/reset.scss";
@import "../../../../assets/styles/com.scss";

@media screen and (min-width: 1200px){
    .main{
        padding-top: 50px;
        display:flex;
        justify-content: space-between;
       .left{
          width: 582px;
          background: rgba(246, 246, 246, 0.5);
          border-radius: 24px;
          border: 1px solid #FFFFFF;
          Backdrop-filter:blur(8px);
          padding: 23px 30px 30px 30px; 
          
          .li{
              position: relative;
              height: 48px;
              line-height: 48px;
              display: flex;
              justify-content: space-between;
              width: 522px;
              font-size: 14px;
              font-family: Montserrat-Regular, Montserrat;
              font-weight: 400;
              color: rgba(60, 60, 67, 0.6);
          } 
          .li_right{
           color: #000000;
          } 
          .li_bottom{
              position: absolute;
              bottom: -14px;
              right: 0;
              font-size: 12px;
              font-family: Montserrat-Regular, Montserrat;
              font-weight: 400;
              color: rgba(60, 60, 67, 0.3);
          }
          .li_address{
             
          }
          i{
            display: inline-block;
            width: 14px;
            height: 14px;
            background: url('../img/icon.svg');
        }
       }
       h1{
       font-size: 18px;
       font-family: Montserrat-SemiBold, Montserrat;
       font-weight: 600;
       color: #000000;
       }
   }
}
/* h5 */
@media screen and (max-width: 450px){
    .main{
     margin-top: 20px;
     margin-bottom: rem(100);
     .left{
        background: #FFFFFF;
        border-radius: 24px;
        padding: 17px 16px;
        margin-bottom: 16px;
        .li{
            position: relative;
            display: flex;
            justify-content: space-between;
            height: 42px;
            font-size: 12px;
            font-family: Montserrat-Regular, Montserrat;
            font-weight: 400;
            color: rgba(60, 60, 67, 0.6);
            line-height: 42px;
        }
        .li_right{
            color: #000000;
        }
        .li_bottom{
            position: absolute;
            bottom: -14px;
            right: 0;
            font-size: 12px;
            font-family: Montserrat-Regular, Montserrat;
            font-weight: 400;
            color: rgba(60, 60, 67, 0.3);
        }
     }
     h1{
        font-size: 14px;
        font-family: Montserrat-SemiBold, Montserrat;
        font-weight: 600;
        color: #000000;
        font-size: 12px;
     }
    }
}