@import "../../../assets/styles/reset.scss";
@import "../../../assets/styles/com.scss";
@media screen and (min-width: 540px) {   
    .popWrap {
        position: fixed;
        left: 0;
        right: 0;
        top: 0;
        bottom: 0;
        background-color: rgba(0,0,0,.4);
        z-index: 9999;
        display: flex;
        align-items: center;
        .pop {
            margin: 0 auto;
            width: 507px;
            background-color: #fff;
            border-radius: 24px;
            z-index: 9999;
            padding: 18px 18px 25px 18px;
            .close {
                display: flex;
                justify-content: flex-end;
                cursor: pointer;
                & img {
                    width: 16px;
                    height: 16px;
                }
            }
            .title {
                font-size: 20px;
                color: #000;
                margin-bottom: 29px;
                font-weight: 600;
            }
            .list {
                display: flex;
                justify-content: space-between;
                .item {
                    margin-bottom: 15px;
                    & p {
                        font-size: 14px;
                        color: #000;
                        margin-bottom: 15px;
                    }
                    .inputWrap {
                        position: relative;
                        border-radius: 18px;
                        overflow: hidden;
                        & input {
                            outline: none;
                            background: #F9F9F9;
                            width: 222px;
                            height: 80px;
                            padding-left: 20px;
                            padding-right: 72px;
                            border: 0;
                            font-size: 24px;
                        }
                        & span {
                            position: absolute;
                            top: 50%;
                            transform: translateY(-50%);
                            right: 12px;
                            min-width: 50px;
                            text-align: center;
                            height: 30px;
                            border-radius: 10px;
                            line-height: 30px;
                            font-size: 14px;
                        }
                    }
                
                }
            }
            .balance {
                font-size: 12px;
                font-weight: 600;
                margin-bottom: 30px;
                color: rgba(60, 60, 67, 0.6);
            }
            .btnWrap {
                display: flex;
                justify-content: center;
                & button {
                    border: 0;
                    cursor: pointer;
                    width: 223px;
                    height: 60px;
                    border-radius: 24px;
                    font-size: 18px;
                    & img {
                        width: 22px;
                        height: 22px;
                        margin-right: 10px;
                    }
                }
            }
        }
    }
}

@media screen and (max-width: 540px) {  
    .popWrap {
        position: fixed;
        left: 0;
        right: 0;
        top: 0;
        bottom: 0;
        background-color: rgba(0,0,0,.4);
        z-index: 9999;
        display: flex;
        align-items: center;
        .pop {
            margin: 0 auto;
            width: rem(343);
            background-color: #fff;
            border-radius: rem(24);
            z-index: 9999;
            padding: rem(16) rem(16) rem(20) rem(16);
            .close {
                display: flex;
                justify-content: flex-end;
                cursor: pointer;
                & img {
                    width: rem(12);
                    height: rem(12);
                }
            }
            .title {
                position: relative;
                top: rem(-10);
                font-size: rem(16);
                color: #000;
                margin-bottom: rem(3);
                font-weight: 600;
            }
            .list {
                display: flex;
                justify-content: space-between;
                .item {
                    margin-bottom: rem(16);
                    & p {
                        font-size: rem(12);
                        color: #000;
                        margin-bottom: 15px;
                    }
                    .inputWrap {
                        position: relative;
                        border-radius: rem(18);
                        overflow: hidden;
                        & input {
                            padding-bottom: rem(30);
                            background: #F9F9F9;
                            width: rem(147);
                            height: rem(72);
                            padding-left: rem(14);
                            padding-right: rem(11);
                            border: 0;
                            font-size: rem(18);
                        }
                        & span {
                            position: absolute;
                            right: rem(11);
                            bottom: rem(12);
                            min-width: rem(40);
                            text-align: center;
                            height: rem(20);
                            border-radius: rem(8);
                            line-height: rem(20);
                            font-size: rem(12);
                        }
                    }
                
                }
            }
            .balance {
                font-size: rem(12);
                font-weight: rem(16);
                margin-bottom: 30px;
                color: rgba(60, 60, 67, 0.6);
            }
            .btnWrap {
                display: flex;
                justify-content: center;
                & button {
                    border: 0;
                    cursor: pointer;
                    width: rem(147);
                    height: rem(50);
                    border-radius: rem(31);
                    font-size: rem(14);
                    & img {
                        width: rem(14);
                        height: rem(14);
                        margin-right: rem(5);
                    }
                }
            }
        }
    }
}

.BSC {
    .btnWrap {
        .takeLp {
            background-color: #080808;
            color: #FFFFFF;
        }
        .takeCoin {
            background-color: #080808;
            color: $bsc_color;
        }
        .confirmLoadingLP {
            background-color: #FFF4BF;
            color: $bsc_color;
        }
        .confirmLoadingCoin {
            background-color: #FFF4BF;
            color: $bsc_color;
        }
        .confirmSuccessLP {
            background-color: #FFF4BF;
            color: $bsc_color;
        }
        .confirmSuccessCoin {
            background-color: #FFF4BF;
            color: $bsc_color;
        }
    }
    .inputWrap {
        & span {
            background-color: #080808;
        }
        .leftUnit {
            color: #fff;
        }
        .rightUnit {
            color: $bsc_color;
        }
    }
    .balance {
        & span {
            color: $bsc_color;
        }
    }
}

.HECO {
    .btnWrap {
        & button {
            color: #FFFFFF;
        }
        .takeLp {
            background-color: $heco_color;
        }
        .takeCoin {
            background-color: #009A7F;
        }
        .confirmLP {
            background-color:  rgba(111, 212, 255, 0.2);
            color: $heco_color;
        }
        .confirmCoin {
            background-color:rgba(3, 173, 144, 0.2);
            color: #009A7F
        }
        .confirmSuccessLP {
            background-color:  rgba(111, 212, 255, 0.2);
            color: $heco_color;
        }
        .confirmSuccessCoin {
            background-color:rgba(3, 173, 144, 0.2);
            color: #009A7F
        }
    }
    .inputWrap {
        .leftUnit {
            background-color: $heco_color;
            color: #fff;
        }
        .rightUnit {
            background-color: #009A7F;
            color: #fff;
        }
    }
    .balance {
        & span {
            color: $heco_color
        }
    }
}